<template >
  <div class="card-login">
    <el-row class="card-title">
      <el-col> <span>Register Form</span></el-col>
    </el-row>

    <div class="card-login">
      <el-card class="box-card">
        <el-alert v-if="message" :title="message" type="error" :value="message">
        </el-alert>
        <el-form
          :model="loginForm"
          :rules="rules"
          ref="loginForm"
          status-icon
          :label-position="labelPosition"
          label-width="100px"
        >
          <el-form-item label="Username" prop="username">
            <el-input
              v-model="loginForm.username"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="Password" prop="password">
            <el-input
              type="password"
              v-model="loginForm.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <div class="btn-login-form">
              <el-button
                type="primary"
                :disabled="loading"
                plain
                @click.prevent="login(loginForm)"
              >
                <span
                  v-show="loading"
                  class="spinner-border spinner-border-sm"
                ></span
                >Login</el-button
              >
            </div>
            <div class="not-account">
              <p>Not register?</p>
              &nbsp;
              <a @click.prevent="redirectRegister"> Create an account. </a>
            </div>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("Please input the password"));
      } else {
        // if (this.ruleForm.checkPass !== "") {
        //   this.$refs.ruleForm.validateField("checkPass");
        // }
        // Password is not correct
        callback();
      }
    };
    return {
      loading: false,
      message: "",
      labelPosition: "top",
      loginForm: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          {
            required: true,
            message: "Please input your email",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            validator: validatePass,
            trigger: "blur",
          },
        ],
      },
      methods: {},
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/profile");
    }
  },
  methods: {
    login(user) {
      this.loading = true;
      this.$store.dispatch("auth/LOGIN", user).then(
        () => {
          this.$router.push("/profile");
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
    redirectRegister() {
      this.$router.push("/register");
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log("submit!");
          this.$router.push("/");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
};
</script>


